import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { BonCasseHtmlComponent } from './bon-casse-html/bon-casse-html.component';
import { ListBonCasseComponent } from './list-bon-casse/list-bon-casse.component';

const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: 'list',
        component: ListBonCasseComponent
      },
      {
        path: 'ajout',
        component: BonCasseHtmlComponent
      },
      {
        path: 'modifier/:id',
        component: BonCasseHtmlComponent
      },
      {
        path: 'details/:id',
        component: BonCasseHtmlComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BonCasseRoutingModule { }
